<template lang="en">
    <div>
        <div class="my-popups">
            <div class="modal d-block ">
    
                <div class="modal-dialog ui-draggable" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{ title }}</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                            <section id="main-content" class="main-content">
                                <section class="main-content">
                                    <div class="container-fluid p-0">
                                        <div class="p-0">
                                            <dynamic-form ref="dynamicForm" class="daterangecontrolHS" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                                            </dynamic-form>
                                        </div>
                                    </div>
                                </section>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </template>
    
    <style>
    .singleselectlook .multiselect__select::before {
        z-index: 99;
    }
    
    .singleselectlook .multiselect__tags {
        padding: 8px 30px 0 8px !important;
        min-height: 36px !important;
    }
    
    .singleselectlook .multiselect__input {
        margin-bottom: 0px;
    }
    </style>
    
    <script>
    import {
        defineComponent,
    } from '@vue/composition-api'
    import {
        extend,
        ValidationObserver,
        ValidationProvider
    } from 'vee-validate';
    import {
        ConvertCustomFieldIntoDynamicFormObj,
        ConvertDynamicFormObjToCustomFieldObj
    } from "@/assets/common/vue-common.js"
    import DataService from '../../services/DataService';
    import moment from 'moment'
    extend('required', {
        validate(value) {
            console.log("checkk")
            return {
                required: true,
                valid: ['', null, undefined].indexOf(value) === -1
            };
    
        },
        message: 'The {_field_} is required'
        ///computesRequired: true
    });
    export default defineComponent({
        name: "Manage Departemts",
        components: {
            ValidationObserver,
            ValidationProvider
        },
        props: {
            addApiUrl: {
                type: String
            },
            emitName: {
                type: String
            },
            moduleName: {
                type: String
            },
            subModule: {
                type: String
            },
            title: {
                type: String
            },
            recordId: {
                type: Number
            }
        },
    
        created: function () {
            let id = this.recordId ? this.recordId : 0
            this.getForm(id)
        },
        methods: {
            getForm: function (id) {
                var ths = this;
                ths.isLoading = true;
                var url = `recordId=${id}&moduleName=${this.moduleName}&subModuleCode=${this.subModule}`;
                DataService.GetDynamicForm(url).then(function (response) {
                    if (response != null && response.data != null) {
                        ths.isMapFeature = response.data.isFsmLocating;
                        // response.data.data                    
                        ths.FormSchema = ths.$refs.dynamicForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                        console.log(ths.FormSchema);
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (group.group_name == "General Information" || group.group_name == "GENERAL INFORMATION") {
                                    group.visibility = false;
                                    if (field.name == "user_name" || field.name == "password" || field.name == "signature") {
                                        field.visibility = false;
                                    }
    
                                    if (field.name == "break_duration") {
                                        field.config.is24hr = true;
                                        //ths.$refs.dynamicForm.updateProperty("break_duration", "config.is24hr", true);
                                        //ths.$refs.dynamicForm.UpdateKeyValue();
                                        //ths.$refs.dynamicForm.updateProperty("break_duration", "config.format", "hh:mm A");
                                    }
    
                                    // ths.$refs.AddTicketForm.UpdateKeyValue();
                                    // if (field.name == "break_duration") {
                                    //     field.config.format ="hh:mm A"
                                    // }
                                    if (field.name == "attachment") {
                                        field.config = {
                                            maxSize: 5,
                                            accept: [
                                                {
                                                    fileType: 'doc', // Document type
                                                    iconClass: 'fa-file-word-o' // Icon class for documents
                                                },
                                                {
                                                    fileType: 'docx', // Document type
                                                    iconClass: 'fa-file-word-o' // Icon class for documents
                                                },
                                                {
                                                    fileType: 'txt', // Text file type
                                                    iconClass: 'fa-file-text-o' // Icon class for text files
                                                },
                                                {
                                                    fileType: 'jpg', // Image types
                                                    iconClass: 'fa-file-image-o' // Icon class for images
                                                },
                                                {
                                                    fileType: 'jpeg', // Image types
                                                    iconClass: 'fa-file-image-o' // Icon class for images
                                                },
                                                {
                                                    fileType: 'png', // Image types
                                                    iconClass: 'fa-file-image-o' // Icon class for images
                                                },
                                                {
                                                    fileType: 'gif', // Image types
                                                    iconClass: 'fa-file-image-o' // Icon class for images
                                                },
                                                {
                                                    fileType: 'bmp', // Image types
                                                    iconClass: 'fa-file-image-o' // Icon class for images
                                                },
                                                // {
                                                //     fileType: 'mp4', // Video types
                                                //     iconClass: 'fa-file-video-o' // Icon class for videos
                                                // },
                                                // {
                                                //     fileType: 'mov', // Video types
                                                //     iconClass: 'fa-file-video-o' // Icon class for videos
                                                // },
                                                // Add more image, document, text, and video types as needed
                                            ],
                                            multiple: false,
                                        }
                                    }
                                }
                                if (field.name == "shift_hours") {
                                    field.disabled = true;
                                    field.placeholder = "Shift Hours"
                                }
    
                                field.config.onChange = ths.OnChangeEventsforField;
                                //ths.$refs.dynamicForm.updateProperty("break_duration", "config.format", "hh:mm A");
                            });
                        });
    
                        if (ths.FormSchema.length > 0) {
                            ths.FormSchema[0].visibility = false
                        }
                        ths.isLoading = false;
                    }
                });
            },
            OnChangeEventsforField: function (x, y, z) {
                let ths = this;
                if (x == "FILL_DEPENDENT") {
                    y.config.options = [];
                    var url = `id=&moduleName=${ths.moduleName}&type=${y.config.fieldCode}&search=&refId=${z}`;
                    DataService.GetDDLData(url).then(function (response) {
                        if (response.data) {
                            var json = response.data;
                            if (json != null) {
                                y.config.options = response.data.DATA;
                                ths.$refs.dynamicForm.UpdateKeyValue();
                            }
                        }
                    });
                } else {
                    if(y.name == "payment_gateway_code" && y.config.options.filter((i) => i.value == y.value)[0].name == "Authorize") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "user_name") {
                                    field.visibility =  true;
                                    field.label = "API Login ID"
                                }
                                if(field.name == "password") {
                                    field.visibility =  true;
                                    field.label = "Transaction Key"
                                }
                                if(field.name == "signature") {
                                    field.visibility =  false;
                                }
                            });
                            // && y.config.options.map((i) => i.name == "Paypal")
                        });
                    } else if (y.name == "payment_gateway_code" && y.config.options.filter((i) => i.value == y.value)[0].name == "Paypal") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "user_name") {
                                    field.visibility =  true;
                                    field.label = "User Name"
                                }
                                if(field.name == "password") {
                                    field.visibility =  true;
                                    field.label = "Password"
                                }
                                if(field.name == "signature") {
                                    field.visibility =  true;
                                }
                            });
                        });
                    } else {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "user_name") {
                                    field.visibility =  true;
                                    field.label = "Secret Key"
                                }
                                if(field.name == "password") {
                                    field.visibility =  true;
                                    field.label = "Publishable Key"
                                }
                                if(field.name == "signature") {
                                    field.visibility =  false;
                                }
                            });
                        });
                    }
                }
                if(y.name == "attachment") {
                    ths.GetBase64String(y.value);
                }
                if(y.name == "start_time") {
                    ths.startTime = ths.$options.filters.formatTime(y.value);
                }
                if(y.name == "end_time") {
                    ths.endTime = ths.$options.filters.formatTime(y.value);
                }
                ths.hoursDifference = ths.calculateHoursDifference(ths.startTime, ths.endTime);

                if(y.name == "location_country") {
                    console.log("country", y.value);
                    this.GetStateDDL(y.value);
                }
    
            },
            
            GetStateDDL: async function (id) {
                var vm = this;
                var CountryId = 0;
                CountryId = id;
                console.log(CountryId);
                var url = `id=&&moduleName=Location&type=${'STATE'}&search=&refId=` + CountryId;
                try {
                    const response = await DataService.GetDDLData(url);
                    // console.log("Response:", response.data);
                    // console.log("FormSchema:", vm.FormSchema);
    
                    if (response.data && response.data.DATA) {
                        // response.data.DATA.forEach((item, index) => {
                        //     // console.log("Item:", item);
                        //     // vm.FormSchema[0].Data[18].config.options.push({
                        //     //     value: item.value,
                        //     //     name: item.name
                        //     // });
    
                        //     console.log("Updated FormSchema:", vm.FormSchema[0].Data[18]);
                        // });                } else {
                        ths.$refs.dynamicForm.updateProperty("on_behalf_consumer_id", "config.options", response.data.DATA);
                        console.log("No data received from the API");
                    } else {
                        if(y.name == "payment_gateway_code" && y.config.OptionsForBind[0].name == "Authorize"){
                            ths.FormSchema.forEach((group, gi) => {
                                group.Data.forEach((field, fi) => {
                                    if(field.name == "user_name") {
                                        field.visibility =  true;
                                        field.label = "API Login ID"
                                    }
                                    if(field.name == "password") {
                                        field.visibility =  true;
                                        field.label = "Transaction Key"
                                    }
                                    if(field.name == "signature") {
                                        field.visibility =  false;
                                    }
                                });
                            });
                        } else if (y.name == "payment_gateway_code" && y.config.OptionsForBind[1].name == "Paypal") {
                            ths.FormSchema.forEach((group, gi) => {
                                group.Data.forEach((field, fi) => {
                                    if(field.name == "user_name") {
                                        field.visibility =  true;
                                        field.label = "User Name"
                                    }
                                    if(field.name == "password") {
                                        field.visibility =  true;
                                        field.label = "Password"
                                    }
                                    if(field.name == "signature") {
                                        field.visibility =  true;
                                    }
                                });
                            });
                        } else {
                            ths.FormSchema.forEach((group, gi) => {
                                group.Data.forEach((field, fi) => {
                                    if(field.name == "user_name") {
                                        field.visibility =  true;
                                        field.label = "Secret Key"
                                    }
                                    if(field.name == "password") {
                                        field.visibility =  true;
                                        field.label = "Publishable Key"
                                    }
                                    if(field.name == "signature") {
                                        field.visibility =  false;
                                    }
                                });
                            });
                        }
                    }
                }
                catch (ex) {
    
                }
            },
            async onSubmit() {
                let obj = ConvertDynamicFormObjToCustomFieldObj(this.FormSchema)
                if (this.moduleName == 'Break') {
                    //console.log("checkkkk",obj)
                    obj.find(function (entry) {
                        if (entry.field_name == 'break_duration') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                }
                if (this.moduleName == 'Shift') {                
                    obj.find(function (entry) {
                        if (entry.field_name == 'start_time') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                }
                DataService.DepartmentAdd(obj, this.addApiUrl).then((res) => {
                    if (res.data.Result != null && res.data.Result.MAIN_ID > 0) {
                        this.ShowAlert(this.$t('SavedSuccessfully'), "success", false, this.$t("Alert"));
                        this.$emit(this.emitName)
                    } else {
                        //this.ShowAlert(this.$t(res.data.Status), "danger", false, this.$t("Alert"));
                        if(this.$t(res.data.Status) == 'FAILURE'){
                            this.ShowAlert(this.$t('ErrorWhileSavingRecords'), "danger", false, this.$t("Alert"));
                        }
                        else{
                            this.ShowAlert(this.$t('RecordAlreadyExists'), "danger", false, this.$t("Alert"));
                        }
    
                            
                        
                    }
    
                })
            },
            closePopup() {
                this.$emit('close')
    
            },
    
        },
        data() {
            return {
                editor: this.CLASSIC_EDITOR,
                department: {
                    name: '',
                    status: {
                        name: "active",
                        id: 1
                    },
                    visibaleToclients: false,
                    visibaleToTicting: false,
                    description: '',
                },
                buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
                ],
                FormSchema: [],
                isLoading: false
            }
    
        },
    
    });
    </script>
    